import React,{Component} from 'react'
import Input from './Input.js'
import List from './List.js'

class TodoList extends Component {
    constructor(){
        super();
        this.state={
            inpValue:'',
            todos:[]
        }
        this.handleChange=this.handleChange.bind(this);
    }

    handleChange=(e)=>{
        this.setState({
            inpValue:e.target.value
        })
    }

    addToDo=()=>{
        if(this.state.inpValue==''){
            return;
        }
        this.setState({
           todos:[{
                title:this.state.inpValue,
                done:false
        },...this.state.todos],
        });
        this.state.inpValue=''
    }

    delToDo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>index!=idx,()=>{
                localStorage.setItem('todos',JSON.stringify(this.state.todos));
            })
        });
    }

    toggle=(index)=>{
        let newTodos=JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done=!newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }

    componentDidMount(){
        let todos=localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(){
        localStorage.setItem('todos',JSON.stringify(this.state.todos))
    }

    renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
                return <div key={idx} style={{backgroundColor:'white',margin:'8px'}}>
                <input onClick={()=>this.toggle(idx)} checked={done} type='checkbox'/>&nbsp;&nbsp;
                <span dangerouslySetInnerHTML={{__html:todo.title}}></span>&nbsp;&nbsp;&nbsp;&nbsp;
                <button onClick={()=>this.delToDo(idx)} style={{margin:'5px'}}>删除</button>
            </div>                   
            }   
        })
    }
    
    
    render(){
        const {inpValue,todos}=this.state;
        return (
            <div style={{backgroundColor:'#CDCDCD',fontSize:'16px',width:'100%',height:'100%'}}>
                <Input handleChange={this.handleChange} addToDo={this.addToDo} inpValue={inpValue}/>
                <List renderTodos={this.renderTodos} delToDo={this.delToDo} todos={todos}/> 
            </div>
        );
    }
    
}

export default TodoList;
