import React,{Component} from 'react'

class Input extends Component {
    render() {        
        return (
            <div style={{textAlign:'center'}}>
                <label htmlFor='inp'>todilist&nbsp;&nbsp;</label>
                 {/* 加value，受控组件 */}
                <input id='lnp' ref={inp=>this.input=inp} value={this.props.inpValue} onChange={this.props.handleChange} type="text" autoFocus/> 
                <button onClick={this.props.addToDo}>添加 </button>      
            </div>
            
        )
    }
}

export default Input;